:root {
    --main: #222;
    --error: #f41224;
    --white: #fffff0;
}

body {
    margin: 0;
    background: linear-gradient(270deg, #98d5ff, #c18bff);
    background-size: 400% 400%;
    font-family: "Roboto", sans-serif;
    font-size: 24px;
}

.paper {
    margin: 8px;
    padding: 8px;
    border-radius: 8px;
    box-shadow: 0 -1px 10px rgba(0, 0, 0, 0.05), 0 1px 4px rgba(0, 0, 0, 0.1), 0 10px 30px var(--white);
    background-color: var(--white);
}

@media (min-width: 600px) {
    .paper {
        margin: 10%;
        padding: 16px;
        max-width: 600px;
    }
}

.title {
    font-size: 32px;
}

.field {
    margin-bottom: 16px;
}

.field__label {
    display: block;
}

.field__input {
    font-size: 24px;
    padding: 8px 16px;
    border: 2px solid var(--main);
    border-radius: 4px;
}

.field__input--error {
    border: 2px solid var(--error);
    animation: shake 0.9s both;
    transform: translateZ(0);
    backface-visibility: hidden;
    perspective: 1000px;
}

.field__error {
    display: block;
    color: var(--error);
}

@media (min-width: 600px) {
    .field__error {
        display: inline;
        margin-left: 8px;
    }
}

@keyframes shake {
    10%,
    90% {
        transform: translate3d(-1px, 0, 0);
    }
    20%,
    80% {
        transform: translate3d(1px, 0, 0);
    }
    30%,
    50%,
    70% {
        transform: translate3d(-3px, 0, 0);
    }
    40%,
    60% {
        transform: translate3d(3px, 0, 0);
    }
}

.btn {
    padding: 8px 16px;
    font-size: 24px;
    cursor: pointer;
    border-radius: 4px;
}